import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBackward } from '@fortawesome/free-solid-svg-icons';
import { faPlay } from '@fortawesome/free-solid-svg-icons';
import { faPause } from '@fortawesome/free-solid-svg-icons';
import { faForward } from '@fortawesome/free-solid-svg-icons';
import { faRedo } from '@fortawesome/free-solid-svg-icons';
import { useState, useRef, useEffect } from 'react';
import { songs } from '../constants';

function Player(props) {
	const audioEl = useRef(null);

	const [repeat, setrepeat] = useState(false);
	const [random, setrandom] = useState(false);

	useEffect(() => {
		if (props.playing) {
			audioEl.current.play();
		} else {
			audioEl.current.pause();
		}
	});

	useEffect(() => {
		if (!props.songData.eventSrcCard) {
			props.setSongData(props.songs[props.currentindex]);
		}
	}, [props.currentindex]);

	const skipsong = (forwards = true) => {
		props.setSongData({ ...props.songData, eventSrcCard: false });
		props.setplaying(true);
		if (forwards && !random) {
			props.setcurrentindex(() => {
				let temp = props.currentindex;
				temp = temp + 1;
				if (temp > props.songs.length - 1) {
					temp = 0;
				}
				return temp;
			});
		} else if (!forwards && !random) {
			props.setcurrentindex(() => {
				let temp = props.currentindex;
				temp = temp - 1;
				if (temp < 0) {
					temp = props.songs.length - 1;
				}
				return temp;
			});
		}
		if (forwards && random) {
			props.setcurrentindex(() => {
				let temp = parseInt(Math.random() * (props.songs.length - 0));
				if (temp !== props.currentindex) {
					return temp;
				} else {
					temp = parseInt(Math.random() * (props.songs.length - 0));
					skipsong();
				}
			});
		} else if (!forwards && random) {
			props.setcurrentindex(() => {
				let temp = parseInt(Math.random() * (props.songs.length - 0));
				console.log(temp);
				if (temp !== props.currentindex) {
					console.log(temp);
					return temp;
				} else {
					temp = parseInt(Math.random() * (props.songs.length - 0));
					skipsong(false);
				}
			});
		}
	};
	const playnextsong = () => {
		props.setSongData({ ...props.songData, eventSrcCard: false });

		console.log(props.currentindex + ' currentindex');
		if (repeat) {
			document.getElementById('audio').currentTime = 0;
			audioEl.current.play();
			return;
		} else {
			props.setcurrentindex(() => {
				let temp = props.currentindex;
				temp = temp + 1;
				if (temp > props.songs.length - 1) {
					props.setplaying(false);
					temp = 0;
				}
				return temp;
			});
		}
	};
	return (
		<>
			<div className="App">
				<p style={{ margin: '22px' }}>Playing Now</p>
				<img src={props.songData.img} alt="thumbnail" />
				<div>
					<h1 style={{ margin: '10px' }}>{props.songData.name}</h1>
					<h3 style={{ margin: '10px' }}>{props.songData.artist}</h3>
				</div>
				<div>
					<button
						title="Shuffle"
						onClick={() => setrandom(!random)}
						style={{
							backgroundColor: random
								? 'rgb(112, 112, 168)'
								: 'rgb(49, 37, 37)',
							borderRadius: '50%',
							border: 'none',
						}}
					>
						S
					</button>
					<button className="forward" onClick={() => skipsong(false)}>
						<FontAwesomeIcon icon={faBackward} />
					</button>
					<button
						className="play"
						onClick={() => props.setplaying(!props.playing)}
					>
						<FontAwesomeIcon
							icon={props.playing ? faPause : faPlay}
						/>
					</button>
					<button className="backward" onClick={() => skipsong()}>
						<FontAwesomeIcon icon={faForward} />
					</button>
					<button
						title="Repeat"
						style={{
							backgroundColor: repeat
								? 'rgb(112, 112, 168)'
								: 'rgb(49, 37, 37)',
							borderRadius: '50%',
							border: 'none',
						}}
						onClick={() => setrepeat(!repeat)}
					>
						<FontAwesomeIcon icon={faRedo} />
					</button>
				</div>
				<audio
					id="audio"
					onEnded={() => playnextsong()}
					src={props.songData.src}
					controls
					ref={audioEl}
				></audio>
				{props.currentindex + 1 !== props.songLength && !random ? (
					<p style={{ margin: '10px' }}>
						Next Song: {props.songs[props.currentindex + 1].name} by{' '}
						{props.songs[props.currentindex + 1].artist}
					</p>
				) : !random ? (
					<p style={{ margin: '10px' }}>
						Next Song: {props.songs[0].name} by{' '}
						{props.songs[0].artist}
					</p>
				) : (
					<p></p>
				)}
			</div>
		</>
	);
}

export default Player;
