import Player from './Components/Player';
import Playlist from './Components/Playlist';
import { useState } from 'react';
import { songs } from './constants';

function Home({ songData, setSongData }) {
	const [playing, setplaying] = useState(false);

	const [currentindex, setcurrentindex] = useState(0);
	return (
		<>
			{/* <nav class="navbar navbar-light">
        <h1 class="navbar-brand mb-0 h1 text-white">Music App</h1>
      </nav> */}

			<div className="cont">
				<Playlist
					playing={playing}
					setplaying={setplaying}
					currentindex={currentindex}
					setcurrentindex={setcurrentindex}
					songs={songs}
				/>
				<div
					style={{
						display: 'flex',
						flexDirection: 'column',
						alignItems: 'center',
						width: '82vw',
					}}
				>
					<div>
						<Player
							songData={songData}
							setSongData={setSongData}
							playing={playing}
							setplaying={setplaying}
							songs={songs}
							songLength={songs.length}
							currentindex={currentindex}
							setcurrentindex={setcurrentindex}
						/>
					</div>
				</div>
			</div>

			<footer className="page-footer font-small text-white">
				<div className="footer-copyright text-center py-3">
					© 2020 Copyright: All Rights Reserved | Terms and Conditions
				</div>
			</footer>
		</>
	);
}

export default Home;
