import React from 'react';
import { events } from '../constants';
import Card from './Card';
const RecommendSongs = ({ setSongData }) => {
	const eventsList = events.map((event, index) => (
		<Card
			id={index}
			name={event.name}
			img={event.img}
			artist={event.artist}
			language={event.language}
			events={events}
			setSongData={setSongData}
		/>
	));
	return (
		<>
			<div className="container-fluid mt-3">
				<h1 class="text-primary m-3">Recommend Songs</h1>
				<div className="d-flex flex-wrap justify-content-evenly">
					{eventsList}
				</div>
			</div>
			<footer id="footer" className="page-footer font-small text-white">
				<div className="footer-copyright text-center py-3">
					© 2020 Copyright: All Rights Reserved | Terms and Conditions
				</div>
			</footer>
		</>
	);
};

export default RecommendSongs;
