import React from 'react';
import { useHistory } from 'react-router';
import { songs } from '../constants';
const Card = (props) => {
	const history = useHistory();
	const addsong = () => {
		props.setSongData({ ...props.events[props.id], eventSrcCard: true });
		const tempSongs = [...songs];
		songs.length = 0;
		songs.push(
			{ ...props.events[props.id], eventSrcCard: true },
			...tempSongs
		);
		history.push('/');
	};
	return (
		<div
			onClick={() => addsong()}
			className="card m-3"
			style={{ width: '16rem' }}
		>
			<img className="card-img-top" src={props.img} alt="Card cap" />
			<div className="card-body">
				<h5 className="card-title">{props.name}</h5>
				<p className="card-text">{props.artist}</p>
				{/* <a href="#" className="btn btn-primary">Go somewhere</a> */}
			</div>
		</div>
	);
};

export default Card;
