import React from 'react'

const LogIn = () => {
    return (
        <>
            
                <div class="row justify-content-center align-items-center" style={{height:"84.5vh",width:"100vw"}}>
                    <form class="pt-3 col-10 col-sm-8 col-md-5 col-xl-3 d-flex flex-column justify-content-center" style={{border:"2px solid grey",height:"50vh",borderRadius:"4px"}}>
                        <div class="form-group">
                            <label for="exampleFormControlInput1">Email address</label>
                            <input type="email" class="form-control" id="exampleFormControlInput1" placeholder="name@example.com" />
                        </div>
                        <div class="form-group">
                            <label for="exampleFormControlInput1">Password</label>
                            <input type="password" class="form-control" id="exampleFormControlInput1" placeholder="Password" />
                        </div>
                        <div class="form-group text-center pt-sm-2">
                            <button type="button" class="btn btn-outline-primary mx-auto">login</button>
                        </div>
                        <div class="form-group text-center pt-md-2">
                            <h6 className="text-success">Don't have an account? <a href="#">SignIn</a> here.</h6>
                        </div>
                    </form>
                </div>
            <footer id="footer" className="page-footer font-small text-white">
                <div className="footer-copyright text-center py-3">© 2020 Copyright: All Rights Reserved | Terms and Conditions
                </div>
            </footer>
        </>
    )
}

export default LogIn;
