import React from 'react'
const Playlist = (props) => {
    const songname=props.songs.map((song,index)=>
        <li  onClick={()=>{props.setcurrentindex(index);props.setplaying(true)}} key={index} style={{color: (index===props.currentindex) ? "yellow" : "white",cursor:"pointer"}}>{song.name}</li>
    );
    return (
        <div className="playlist">
            <h1 style={{textAlign:"center",marginTop:"20px"}}>PlayList</h1>
            <ul>{songname}</ul>
        </div>
    )
}

export default Playlist;